












































































import popupLayer from '../../../components/popup-layer/popup-layer.vue';
// 选择时间的选择
import MxDatePicker from '../../../components/mx-datepicker/mx-datepicker.vue'
import dateutll from '../../../common/util.js'
var _self;
export default {
	components: {
		popupLayer,
		MxDatePicker
	},
	data() {
		return {
			direction: 'left',
			allpeople: true, //选择筛选所有人
			containcontrol: true, //包含管理者的筛选
			gonggong: false,
			wenhua: false,
			jixiao: false,
			guizhang: false,
			nengli: false,
			kaoqin: false,

			// 时间控件
			date: '2019/01/01',
			type: 'date',
			value: '',
			showPicker: false,

			showpop: false

		};
	},
	onLoad() {
		_self = this
	},
	onReady() {
		this.date = dateutll.dateUtils.getNowFormatDate()
	},
	methods: {

		showpup: function() {
			// 弹出弹窗
			this.$refs.popup.show() // 弹出
			this.showpop = true
		},
		popudown: function() {
			this.$refs.popup.close()
			this.showpop = false
		},
		quanyuanpaiming: function() {
			_self.allpeople = true;
			_self.zhiwei = false
		},
		zhiweipaiming: function() {
			_self.allpeople = false;
			_self.zhiwei = true
		},
		contain: function() {
			_self.containcontrol = true
		},
		containnot: function() {
			_self.containcontrol = false
		},
		gonggong1() {
			if (this.gonggong)
				this.gonggong = false
			else
				this.gonggong = true
		},
		qiyewenhua1() {
			if (this.wenhua)
				this.wenhua = false
			else
				this.wenhua = true
		},
		jixiao1() {
			if (this.jixiao)
				this.jixiao = false
			else
				this.jixiao = true
		},
		guizhang1() {
			if (this.guizhang)
				this.guizhang = false
			else
				this.guizhang = true
		},
		nengli1() {
			if (this.nengli)
				this.nengli = false
			else
				this.nengli = true
		},
		kaoqin1() {
			if (this.kaoqin)
				this.kaoqin = false
			else
				this.kaoqin = true
		},
		choisetime(type) {
			//弹出时间的选择框
			this.type = type;
			this.showPicker = true;
			this.value = this[type];
		},
		onSelected1(e) { //选择时间后
			this.showPicker = false;
			if (e) {
				this[this.type] = e.value;
			}
		}
	},
	onPullDownRefresh: function() {
		// 执行下拉刷新的方法
		setTimeout(function() {
			uni.stopPullDownRefresh()
		}, 1000)
	},
	onBackPress: function() {
		if (this.showPicker) {
			this.showPicker = false;
			return true
		}
		if (this.showpop) {
			this.$refs.popup.close()
			this.showpop = false
			return true
		}
	}

}
